<template>
  <div class="get-jifen-layout">
    <header class="clearfix pcen-jifen-bar">
      <a onclick="window.history.go(-1);" class="jifen-back-btn"></a>
      <h2 class="jifen-title">积分规则</h2>
    </header>
    <main class="jifen-get-main" v-show="!isNoData">
      <div class="jifen-info" v-for="(item,index) in jiFenList">
        {{index+1}}.{{item.credit_item_name}}
        <p>每次可获得{{item.item_credit}}积分,每天最多有{{item.restrict_max_num}}次机会可获得对应的积分</p>
      </div>

    </main>
    <NoData v-show="isNoData"></NoData>
  </div>
</template>
<script>
  import Api from '../../api/api';
  import NoData from '../nodata/NoData.vue';
  import {getJiFenInfo} from '../../api/jifen';
  export default{
      components:{
        NoData
      },
      data(){
        return{
            jiFenList:[],
            isNoData:false
        }
      },
      mounted(){
        this.$nextTick(function () {
          this.getJiFen();
        });
      },
      methods:{
        getJiFen(){
            const _this = this;
            getJiFenInfo(Api.USERJIFEN).then((res)=>{
              _this.initJifenData(res);
            },(err)=>{
              _this.dealErr();
            });
        },
        dealErr(){
          this.$store.dispatch('hideLoading');
          this.isNoData = true;
        },
        initJifenData(res){
          if(res.status === 200 && res.data.data!==null){
              this.jiFenList = res.data.data.detail;
              const len = this.jiFenList.length;
              len > 0 ? this.isNoData = false:this.isNoData = true;
          }else{
            this.dealErr();
          }
        },
      },
  }
</script>
<style scoped>
  @import "../../assets/css/getjifen.css";
</style>



